<template>
  <div id="app" :class="{ background: hasBackground }">
    <app-navigation></app-navigation>
    <transition name="fade" mode="out-in">
      <router-view />
    </transition>
  </div>
</template>

<script>
import VueJwtDecode from "vue-jwt-decode";
import AppNavigation from "./common/AppNavigation";
import { mapGetters } from "vuex";

export default {
  components: {
    AppNavigation
  },
  created() {
    if (this.authenticated) {
      this.$store.dispatch("setUser");
    }
  },
  computed: {
    ...mapGetters(["authenticated"]),
    hasBackground() {
      return this.$route.path === "/users";
    }
  },
  watch: {
    authenticated(token) {
      if (!token) {
        this.$router.push("/");
      }
    }
  }
};
</script>

<style lang="scss">
body,
html {
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(255, 255, 255, 0.2)),
    url("~@/assets/forest1.jpg") no-repeat center center fixed;
  background-size: cover;
  @media (max-width: 768px) {
    background: #fff;
  }
}
#app {
  font-family: "Montserrat", "Avenir", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  min-height: 100%;

  &.background {
    background: #fff;
  }

  .fade-enter-active,
  .fade-leave-active {
    transition-duration: 0.3s;
    transition-property: opacity;
    transition-timing-function: ease;
  }

  .fade-enter,
  .fade-leave-active {
    opacity: 0;
  }
}
</style>
